import React, { useState, useEffect } from "react";
import axios from "axios";
import "./TranslateLibre.css";

const LANGUAGES = [
  { code: "en", label: "English" },
  { code: "vi", label: "Vietnamese" },
];

const STORAGE_KEY = "translateLibreHistory";

const TranslateLibre = () => {
  const [text, setText] = useState("");
  const [translated, setTranslated] = useState("");
  const [loading, setLoading] = useState(false);
  const [sourceLang, setSourceLang] = useState("en");
  const [targetLang, setTargetLang] = useState("vi");
  const [history, setHistory] = useState([]);

  // Load history from localStorage once on mount
  useEffect(() => {
    const saved = localStorage.getItem(STORAGE_KEY);
    if (saved) {
      setHistory(JSON.parse(saved));
    }
  }, []);

  // Save history to localStorage on change
  useEffect(() => {
    localStorage.setItem(STORAGE_KEY, JSON.stringify(history));
  }, [history]);

  const handleTranslate = async () => {
    if (!text.trim()) return;
    setLoading(true);
    try {
      const response = await axios.post(
        "https://translate.argosopentech.com/translate",
        {
          q: text,
          source: sourceLang,
          target: targetLang,
          format: "text",
        },
        {
          headers: { "Content-Type": "application/json" },
        }
      );

      const result = response.data.translatedText;
      setTranslated(result);

      setHistory((prev) => [
        { input: text, output: result, from: sourceLang, to: targetLang },
        ...prev,
      ]);

      setText(""); // Clear input after translate
    } catch (err) {
      console.error("Translation error:", err);
      setTranslated("⚠️ Translation failed. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  const handleSwapLanguages = () => {
    setSourceLang(targetLang);
    setTargetLang(sourceLang);
    setTranslated("");
  };

  const handleClearHistory = () => {
    setHistory([]);
    localStorage.removeItem(STORAGE_KEY);
  };

  return (
    <div className="translator-compact" style={{ flexDirection: "column", gap: 12 }}>
      <div style={{ display: "flex", gap: 12, alignItems: "center" }}>
        <select
          value={sourceLang}
          onChange={(e) => setSourceLang(e.target.value)}
          className="translator-select"
        >
          {LANGUAGES.map((lang) => (
            <option key={lang.code} value={lang.code}>
              {lang.label}
            </option>
          ))}
        </select>

        <button
          onClick={handleSwapLanguages}
          className="translator-swap-btn"
          title="Swap languages"
        >
          🔄
        </button>

        <select
          value={targetLang}
          onChange={(e) => setTargetLang(e.target.value)}
          className="translator-select"
        >
          {LANGUAGES.map((lang) => (
            <option key={lang.code} value={lang.code}>
              {lang.label}
            </option>
          ))}
        </select>
      </div>

      <div style={{ display: "flex", gap: 8 }}>
        <input
          type="text"
          value={text}
          onChange={(e) => setText(e.target.value)}
          placeholder={`${LANGUAGES.find((l) => l.code === sourceLang).label} → ${
            LANGUAGES.find((l) => l.code === targetLang).label
          }`}
          className="translator-input"
          onKeyDown={(e) => e.key === "Enter" && handleTranslate()}
        />
        <button onClick={handleTranslate} disabled={loading} className="translator-btn">
            {loading ? "..." : "→"}
        </button>
      </div>

      {translated && (
        <div className="translator-output">
          <strong>Result:</strong> {translated}
        </div>
      )}

      {history.length > 0 && (
        <div className="translator-history">
          <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
            <h3>History</h3>
            <button onClick={handleClearHistory} className="translator-clear-btn" title="Clear history">
              ❌
            </button>
          </div>

          <ul>
            {history.map((item, i) => (
              <li key={i} className="translator-history-item">
                <code>
                  [{item.from} → {item.to}] "{item.input}" → "{item.output}"
                </code>
              </li>
            ))}
          </ul>
        </div>
      )}
    </div>
  );
};

export default TranslateLibre;
